(function () {

    angular.module('JRDApp', ['ngRoute']);

    function config ($routeProvider, $locationProvider) {
        $routeProvider
            .when('/', {
                templateUrl: '/tweets/getTopics/getTopics.view.html',
                controller: 'getTopicsCtrl',
                controllerAs: 'vm'
            })
            .when('/getUpdates/:name', {
                templateUrl: '/tweets/getUpdates/getUpdates.view.html',
                controller: 'getUpdatesCtrl',
                controllerAs: 'vm'
            })
            .when('/getTopics', {
                templateUrl: '/tweets/getTopics/getTopics.view.html',
                controller: 'loginCtrl',
                controllerAs: 'vm'
            })
            .otherwise({redirectTo: '/'});

        // use the HTML5 History API
        $locationProvider.html5Mode(true);
    }

    angular
        .module('JRDApp')
        .config(['$routeProvider', '$locationProvider', config])

})();
(function () {

	// Factory to connect, emit and listen the socket events from socket.io
	angular
		.module('JRDApp')
		.factory('socketFactory', ['$rootScope', function($rootScope) {
			
			var socket;
			return {
				connect: function(){
					socket = io();
				},

				on: function(eventName, callback){
				  	socket.on(eventName, callback);
				},

				emit: function(eventName, data) {
				  	socket.emit(eventName, data);
				}
			};
	}]);

})();	
(function () {

	angular
		.module('JRDApp')
		.controller('getTopicsCtrl', [ '$location', '$scope', '$http', function($location, $scope, $http) {

			$http.post('/api/loadTopics').then(function(data){
				$scope.trends = data.data;
			}, function(err){
				$scope.trends = [];
			});

			$scope.goToTweets = function(trend) {
				$location.path("/getUpdates/"+encodeURI(trend.name))
			}

		} 
	]);

})();
